import { Component, h, Prop } from '@stencil/core';

@Component({
  tag: 'dc-ui-header-flyout-title-link',
  styleUrl: 'dc-ui-header-flyout-title-link.scss',
  shadow: true,
})
export class DcUiHeaderFlyoutTitleLink {
  @Prop() label: string;
  @Prop() href: string;
  @Prop() disabled: boolean = false;
  @Prop() uppercase: boolean = true;

  componentWillLoad() {
    if (!this.href) {
      this.disabled = true;
    }
  }

  render() {
    return (
      <a
        href={this.href}
        class={{
          disabled: this.disabled,
          title: true,
          uppercase: this.uppercase,
        }}>
        <span>{this.label || <slot />}</span>
      </a>
    );
  }
}
